/* eslint-disable react/jsx-filename-extension */

import React from 'react'

import connect from '@vkontakte/vkui-connect'
import { View, Panel } from '@vkontakte/vkui'

import './App.css'

import Home from './components/Home'
import Result from './components/Result'

const results = ['china', 'pisa', 'everest', 'bigban', 'paris', 'amsterdam', 'taiga', 'prague', 'munich', 'kaluga', 'georgia', 'mongolia', 'israel', 'vatican', 'nepal', 'chernobyl']

class App extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            block: 'home',
            user: null,
            friend: null,
            result: ''
        }
    }

    componentDidMount() {
        connect.send('VKWebAppInit', {})

        connect.subscribe((e) => {
            console.log(e.detail)

            document.getElementById('debug').innerHTML += `<br/>${e.detail.type}`

            switch (e.detail.type) {
            case 'VKWebAppGetUserInfoResult':
                this.setState({ user: e.detail.data })
                break
            case 'VKWebAppGetFriendsResult':
                this.setState({
                    block: 'result',
                    friend: e.data.users[0],
                    result: results[Math.floor(Math.random() * results.length)]
                })
                break
            default:
                break
            }
        })

        connect.send('VKWebAppGetUserInfo', {})

        connect.send('VKWebAppGetAuthToken', { app_id: 7105859, scope: 'friends,status' })
    }

    render() {
        const { block, user, friend, result } = this.state

        return (
            <View activePanel='main'>
                <Panel id='main'>
                    {block === 'home' && <Home
                        user={user}
                    />}
                    {block === 'result' && <Result
                        user={user}
                        friend={friend}
                        result={result}
                    />}
                </Panel>
            </View>
        )
    }
}

export default App
